<template>
    <div class="budget-block">
        <h4 class="titleH4">What's your budget?</h4>
        <p class="description">Find vehicle options that fit your budget.</p>
        <div class="buttons-block">
            <base-button @click="selectValue('Under $250/Month')" :class="{ 'select': value == 'Under $250/Month' }" quiz>Under $250/Month</base-button>
            <base-button @click="selectValue('$250-374/Month')" :class="{ 'select': value == '$250-374/Month' }" quiz>$250-374/Month</base-button>
            <base-button @click="selectValue('$374-500/Month')" :class="{ 'select': value == '$374-500/Month' }" quiz>$374-500/Month</base-button>
            <base-button @click="selectValue('Over $500/Month')" :class="{ 'select': value == 'Over $500/Month' }" quiz>Over $500/Month</base-button>
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
        </div>

    </div>
</template>
<script>
import baseButton from '../ui/base-button.vue'

export default {
    components: { baseButton, },
    data() {
        return {
            value: '',
        }
    },
    computed: {
        disabled() {
            if (this.value != '') return false;
            else return true;
        },
    },
    methods: {
        selectValue(value){
            this.value = value;
        },
        sendValue() {
            this.$emit('budget', this.value);
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";
</style>