<template>
    <div class="monthly-income-block">
        <h4 class="titleH4">How do you earn income?</h4>
        <p class="description">Your employment status will help determine the best vehicle and financing options for you.
        </p>
        <div class="buttons-block">
            <base-button @click="selectValue(1)" :class="{ 'select': value == 1 }" quiz>Long-term disability</base-button>
            <base-button @click="selectValue(2)" :class="{ 'select': value == 2 }" quiz>Spousal support / Alimony</base-button>
            <base-button @click="selectValue(3)" :class="{ 'select': value == 3 }" quiz>Child support</base-button>
            <base-button @click="selectValue(4)" :class="{ 'select': value == 4 }" quiz>Social assistance / Welfare</base-button>
            <base-button @click="selectValue(5)" :class="{ 'select': value == 5 }" quiz>Maternity leave</base-button>
            <base-button @click="selectValue(6)" :class="{ 'select': value == 6 }" quiz>Baby bonus / Child tax</base-button>
            <base-button @click="selectValue(7)" :class="{ 'select': value == 7 }" quiz>Unemployed / EI</base-button>
            <base-button @click="selectValue(8)" :class="{ 'select': value == 8 }" quiz>Other / Not listed</base-button>
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
            <backControl @click="back"></backControl>
        </div>
    </div>
</template>
<script>
import backControl from '../ui/back-control.vue';
import baseButton from '../ui/base-button.vue'
export default {
    components: { baseButton, backControl },
    data() {
        return {
            value: 0,
            disabled: true,
        }
    },
    props:{
        progress:{
            type: Number,
            default: 40
        }
    },
    activated() {
        this.$emit('progress', this.progress);
    },
    methods: {
        selectValue(id) {
            this.disabled = false;
            this.value = id;
        },
        sendValue() {
            this.$emit('howEarnIncome', this.value);
        },
        back() {
            this.$emit('back');
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";
</style>