<template>
    <div class="page content-container">
        <div class="filter-block">
            <filter-component :filterMenu="filterMenu" @closeFilterMenu="CloseFilterMenu"></filter-component>
        </div>
        <div class="products-block">
            <div class="navigation-block">
                <div class="search-block">
                    <div class="search-filter" @click="openFilterMenu">
                        <img src="@/assets/img/svg/Filter.svg" alt="">
                        <p class="search-filter-text">
                            Search Filter
                        </p>
                    </div>
                    <div class="input-block">
                        <input type="text" placeholder="Find a dream car..." v-model="search" id="input-navigation" class="input-navigation">
                        <label for="input-navigation" class="icon"></label>
                    </div>
                    <button class="button-icon" @click="copyLink"></button>
                </div>
                <div class="sort-block">
                    <label class="sort-label">Sorted by</label>
                    <div class="select-sort-block">
                        <select class="select-sort" name="" id="sort" v-model="selectedOption"
                            @click="this.selectSort = !this.selectSort" @blur="this.selectSort = false">
                            <option value="recommendations" selected>Recommendations</option>
                            <option value="newest-inventory">Newest inventory</option>
                            <option value="lowest-price">Lowest price</option>
                            <option value="highest-price">Highest prices</option>
                        </select>
                        <span class="arrow" :class="{ reverse: selectSort }"></span>
                    </div>
                </div>
            </div>
            <pagination-component :data="filteredItems"></pagination-component>
        </div>
    </div>
</template>
<script>
import filterComponent from '@/components/filter-component.vue';
import carPosts from '@/js/car-posts';
import paginationComponent from '@/components/inventory-page/pagination-component.vue';
import { toRef } from 'vue';
export default ({
    components: { filterComponent, paginationComponent },
    data() {
        return {
            carPosts,
            selectSort: false,
            selectedOption: "recommendations",
            searchText: "",
            selectedCategory: "",
            categories: ["category 1", "category 2", "category 3"],
            transmission: '',
            bodyType: '',
            priceS: { id: 1, title: 'Price', value: [3500, 150000], checked: false },
            year: { id: 1, title: 'Year', value: [1980, 2023], checked: false },
            kilometres: { id: 1, title: 'Kilometres', value: 500000, checked: false },
            make: '',
            models: '',
            filterMenu: false,
            search: '',
        }
    },
    provide() {
        return {
            make: toRef(this, "make") ,
            models: toRef(this, "models"),
            transmission: toRef(this, "transmission"),
            bodyTypeR: toRef(this, "bodyType"),
            priceS: toRef(this, "priceS"),
            year: toRef(this, "year"),
            kilometres: toRef(this, "kilometres"),
            SetMake: (make) => {
                this.make = make
            },
            SetModels: (models) => {
                this.models = models
            },
            SetKilometres: (kilometres) => {
                this.kilometres = kilometres
            },
            SetYear: (year) => {
                this.year = year
            },
            SetPrice: (price) => {
                this.priceS = price
            },
            setBodyType: (value) => {
                this.bodyType = value;
            },
            setTransmission: (transmission) => {
                this.transmission = transmission
            }
        }
    },
    methods: {
        copyLink() {
            const url = window.location.href;
            navigator.clipboard.writeText(url)
                .catch((error) => {
                    console.error('Не вдалося скопіювати посилання: ', error);
                });
        },
        openFilterMenu() {
            this.filterMenu = !this.filterMenu;
            document.body.style.overflowY = 'hidden'
        },
        CloseFilterMenu() {
            this.filterMenu = !this.filterMenu;
            document.body.style.overflowY = 'auto'
        }
    },
    computed: {
        filteredItems() {
            let filteredMass = this.carPosts.filter(item => item.price >= this.priceS.value[0] && item.price <= this.priceS.value[1] && item.year >= this.year.value[0] && item.year <= this.year.value[1] && item.kilometres <= this.kilometres.value);
            if (this.selectedOption === "newest-inventory") {
                filteredMass.sort((a, b) => new Date(a.dateAdd) - new Date(b.dateAdd));
            }
            else if (this.selectedOption === "lowest-price") {
                filteredMass.sort((a, b) => a.price - b.price);
            }
            else if (this.selectedOption === "highest-price") {
                filteredMass.sort((a, b) => b.price - a.price);
            }
            if (this.transmission.length) {
                filteredMass = filteredMass.filter(item => this.transmission.some(trans => item.transmission.includes(trans)));
            }
            if (this.bodyType.length) {
                filteredMass = filteredMass.filter(item => this.bodyType.some(type => item.type.includes(type)));
            }
            if (this.make.length) {
                filteredMass = filteredMass.filter(item => this.make.some(type => item.title.includes(type)));
                console.log(filteredMass);
                if (this.models.length) {
                    filteredMass = filteredMass.filter(item => this.models.some(type => item.makeid === type.makeid && item.title.includes(type.title)) || this.models.every(type => item.makeid != type.makeid));

                    console.log(filteredMass);
                }
            }
            
            return filteredMass.filter(item => item.title.toLowerCase().includes(this.search.toLowerCase()));
        },
    },
})
</script>
<style lang="scss" scoped>
@import "@/assets/scss/default/default.scss";

.page {
    display: flex;
    gap: 40px;
}

.filter-block {
    max-width: 300px;
    width: 100%;

    @media (max-width: 968px) {
        position: absolute;
        max-width: none;
        width: 100%;
    }
}

.navigation-block {
    justify-content: space-between;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    column-gap: 20px;
    row-gap: 10px;
    min-width: 624px;
    width: 100%;

    @media (max-width: 968px) {
        min-width: 0;
    }

    @media (max-width: 600px) {
        flex-direction: column;
        align-items: flex-start;
    }

    .sort-block {
        display: flex;
        align-items: center;
        gap: 10px;

        @media (max-width: 600px) {
            max-width: 600px;
            width: 100%;
        }

        .sort-label {
            @include description;
            cursor: pointer;

            @media (max-width: 968px) {
                display: none;
            }
        }

        .select-sort-block {
            position: relative;

            @media (max-width: 600px) {
                max-width: 600px;
                width: 100%;
            }

            .arrow {
                @include arrow-bottomIc;
                position: absolute;
                right: 10px;
                display: flex;
                top: 50%;
                font-size: 24px;
                transition: all 0.5s;
                transform: translate(0, -40%);

                &.reverse {
                    transform: rotate(180deg) translate(0, 50%);
                }
            }

            .select-sort {
                padding: 8px 40px 8px 20px;
                max-width: 240px;
                width: 100%;
                border: 1px solid #D7D7D7;
                -webkit-appearance: none;
                -moz-appearance: none;
                appearance: none;
                cursor: pointer;

                @media (max-width: 600px) {
                    max-width: 600px;
                    width: 100%;
                }
            }
        }
    }

    .search-block {
        display: flex;
        align-items: center;
        gap: 20px;

        @media (max-width: 600px) {
            width: 100%;
            justify-content: space-between;
        }

        .search-filter {
            display: none;

            .search-filter-text {
                @media (max-width: 600px) {
                    display: none;
                }
            }

            @media (max-width: 968px) {
                display: flex;
                align-items: center;
                gap: 10px;
            }
        }

        .input-block {
            position: relative;
            display: inline-block;

            @media (max-width: 600px) {
                max-width: 600px;
                width: 100%;
            }

            .input-navigation {
                padding: 8px 64px 8px 20px;
                max-width: 350px;
                width: 100%;
                border: 1px solid #D7D7D7;

                @media (max-width: 600px) {
                    max-width: 600px;
                    width: 100%;
                }
            }

            .icon {
                @include searchIc;
                position: absolute;
                right: 20px;
                top: 50%;
                transform: translate(0, -45%);
                font-size: 24px;
                cursor: pointer;

                @media (max-width: 968px) {
                    right: 10px;
                }

                @media (max-width: 600px) {
                    right: 10px;
                }
            }
        }

        .button-icon {
            border: none;
            background: none;
            @include shareIc;
            font-size: 22px;
            cursor: pointer;
        }
    }
}

.car-posts-block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;

    .post {
        width: 100%;
        height: 100%;
    }
}

.products-block {
    width: 100%;
}
</style>