<template>
    <div class="employment-status-block">
        <h4 class="titleH4">How long have you been earning this income?</h4>
        <p class="description">Your income details help us make sure your vehicle payments are easy and affordable.</p>
        <div class="input-block">
            <input class="input years" v-model="years" placeholder="Years">
            <input class="input months" v-model="months" placeholder="Months">
        </div>
        <div class="buttons-block">
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
            <backControl @click="back"></backControl>
        </div>
    </div>
</template>
<script>
import backControl from '../ui/back-control.vue';
import baseButton from '../ui/base-button.vue'
export default {
    components: { baseButton, backControl },
    data() {
        return {
            years:'',
            months:'',
        }
    },
    computed: {
        disabled() {
            if (this.years != '' && this.months != '') return false;
            else return true;
        },
        timeEarning(){
            return this.years + ' ' + this.months;
        }
    },
    methods: {
        sendValue() {
            this.$emit('timeEarning', this.timeEarning);
            console.log(this.timeEarning);
        },
        back() {
            this.$emit('back');
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";
.input-block{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 10px;
}
</style>