<template>
  <div class="progress-container">
    <div class="progress-bar" :style="{ width: `${value}%` }"></div>
  </div>
</template>

<script>
  export default {
    name: 'CustomProgress',
    props: {
      value: {
        type: Number,
        required: true,
      },
    },
  };
</script>

<style>
  .progress-container {
    width: 100%;
    height: 8px;
    border: 1px solid #ccc;
    border-radius: 2px;
  }

  .progress-bar {
    height: 100%;
    background: #7380FF;
    border-radius: 2px;
  }
</style>