<template>
    <div class="employment-status-block">
        <h4 class="titleH4">Are you currently working?</h4>
        <p class="description">Your employment status will help determing the best vehicle and financing options for you.
        </p>
        <div class="buttons-block">
            <base-button @click="selectValue('Yes')" :class="{ 'select': value == 'Yes' }" quiz>Yes</base-button>
            <base-button @click="selectValue('No')" :class="{ 'select': value == 'No' }" quiz>No</base-button>
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
            <backControl @click="back"></backControl>
        </div>
    </div>
</template>
<script>
import backControl from '../ui/back-control.vue';
import baseButton from '../ui/base-button.vue'
export default {
    components: { baseButton, backControl },
    data() {
        return {
            value: '',
        }
    },
    computed: {
        disabled() {
            if (this.value != '') return false;
            else return true;
        },
    },
    methods: {
        selectValue(value) {
            this.value = value;
        },
        sendValue() {
            this.$emit('working-status', this.value);
        },
        back() {
            this.$emit('back');
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";
</style>