<template>
    <div class="filter-blocks" :class="{ 'open': filterMenu }">
        <div class="detail-search-block">
            <span class="closeIc" @click="$emit('closeFilterMenu', false)"> </span>
            <p class="titleH5">Detailed search</p>
            <p class="link clear" @click="Clear">Clear filters</p>
        </div>
        <div class="filter-block">
            <make-filter></make-filter>
            <checkbox-filter></checkbox-filter>
            <transmission-filter></transmission-filter>
            <price-filter></price-filter>
            <year-filter></year-filter>
            <kilometres-filter></kilometres-filter>
        </div>
    </div>
</template>
<script>
import makeFilter from './filter/make-filter.vue';
import checkboxFilter from './filter/checkbox-filter.vue'
import transmissionFilter from './filter/transmission-filter.vue';
import priceFilter from './filter/price-filter.vue';
import yearFilter from './filter/year-filter.vue';
import kilometresFilter from './filter/kilometres-filter.vue';
export default ({

    components: { makeFilter, checkboxFilter, transmissionFilter, priceFilter, yearFilter, kilometresFilter },
    inject: ['setBodyType', 'setTransmission', 'SetPrice', 'SetYear', 'SetKilometres', 'SetMake', 'SetModels'],
    props: {
        filterMenu: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        Clear() {
            this.setBodyType('');
            this.setTransmission('');
            this.SetPrice({ id: 1, title: 'Price', value: [3500, 150000], checked: false });
            this.SetYear({ id: 1, title: 'Year', value: [1980, 2023], checked: false });
            this.SetKilometres('');
            this.SetMake('');
            this.SetModels('');
        }
    }
})
</script>
<style lang="scss" scoped>
@import "@/assets/scss/default/default.scss";

.filter-blocks {
    @media (max-width: 968px) {
        width: 100%;
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: center;
        top: 0px;
        left: -100vw;
        background: white;
        z-index: 3;
        padding-left: 24px;
        padding-right: 24px;
        padding-bottom: 24px;
        margin-top: 71px;
        padding-top: 48px;
        height: 100%;
        overflow-y: auto;

        @media (max-width: 600px) {
            padding-left: 20px;
            padding-right: 20px;
        }

        &.open {
            left: 0;
        }
    }

    .detail-search-block {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 30px;
        margin-top: 9px;
        width: 100%;
        gap: 20px;

        @media (max-width: 968px) {
            max-width: 420px;
        }

        @media (max-width: 515px) {
            justify-content: start;
        }

        .clear {
            font-family: 'Gilroy';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 159%;
            cursor: pointer;
            color: #7481FF;
        }

        .closeIc {
            position: absolute;

            @media (max-width: 968px) {
                @include closeIc;
                width: fit-content;
                height: 30px;
                display: inline-block;
                position: absolute;
                left: 24px;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 24px;
            }

            @media (max-width: 515px) {
                left: 0;
                position: relative;
            }
        }
    }

    .filter-block {
        display: flex;
        flex-direction: column;
        gap: 10px;
        width: 100%;

        @media (max-width: 968px) {
            max-width: 420px;
        }
    }
}
</style>