<template>
    <div class="pag" v-if="paginatedData.length">
        <div class="pagination-block">
            <div class="post" v-for="(post, index) in paginatedData" :key="index">
                <car-post-component :item="post"></car-post-component>
            </div>
        </div>
        <div class="pagination-button">
            <button class="button-rectangle" v-for="(page, index) in totalPages" :key="index" :disabled="currentPage === page"  @click="OpenPage(page)"></button>
        </div>
    </div>
    <div class="else-block" v-else>
        <img src="@/assets/img/svg/404-car.svg" alt="">
        <p class="titleText">Unfortunately there are no matches for your query.</p>
        <p class="description">Try using other filter settings or request a car of your choice.</p>
        <router-link :to="'/quiz/0'" class="Request">Request a car</router-link>
    </div>
</template>
<script>
import CarPostComponent from '../posts/car-post-component.vue';
export default {
    data() {
        return {
            screenWidth: window.innerWidth,
            currentPage: 1,
        }
    },
    props: {
        data: {
            type: Array,
            required: true
        },
    },
    components: {
        CarPostComponent,
    },
    mounted() {
        window.addEventListener("resize", this.handleResize);
    },
    beforeUnmount() {
        window.removeEventListener("resize", this.handleResize);
    },
    computed: {
        paginatedData() {
            const startIndex = (this.currentPage - 1) * this.itemsPerPage;
            const endIndex = startIndex + this.itemsPerPage;
            return this.data.slice(startIndex, endIndex);
        },
        itemsPerPage() {
            if (this.screenWidth < 969 && this.screenWidth > 600) {
                return 6;
            } else if (this.screenWidth < 601) {
                return 6;
            } else {
                return 6;
            }

        },
        totalPages() {
            return Math.ceil(this.data.length / this.itemsPerPage);
        }
    },
    methods: {
        OpenPage(page){
            this.currentPage = page;
        },
        handleResize() {
            this.screenWidth = window.innerWidth;
        },
    },
    watch:{
        data: {
            handler() {
                this.currentPage = 1;
            }
        },
    }
};
</script>
<style scoped lang="scss">
@import '@/assets/scss/default/default.scss';

.pagination-block {

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;

    @media (max-width: $deskToTablet) {
        grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: $tabletToMobile) {
        grid-template-columns: repeat(1, 1fr);
    }

    .post {
        min-height: 100%;
        min-width: 100%;
    }
}

.else-block{
    background-color: $color3;
    padding: 60px;
    box-shadow: 0px 20px 25px rgba(0, 0, 0, 0.1);
    @media (max-width: 600px) {
        padding: 20px;
    }
    .titleText{
        margin-top: 20px;
    }
    .description{
        margin-top: 10px;
    }
    .Request{
        display: block;
        margin-top: 20px;
        @include buttonText;
        text-align: start;
        cursor: pointer;
        color: #7481FF;
        &:hover{
            color: $color4;
        }
    }
}
.pag {
    display: flex;
    flex-direction: column;
    height: 100%;
    gap: 40px;
    width: 100%;
    @media (max-width: 600px) {
        gap: 30px;
    }
}

.pagination-button {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 10px;
    .button-rectangle {
        border-radius: 50%;
        width: 10px;
        height: 10px;
        border: none;
        background-color: #606276;
        opacity: 0.3;
        cursor: pointer;

        &:disabled {
            background: #606276;
            opacity: 1;
            cursor: auto;
        }
    }
}</style>