<template>
    <div class="page content-container">
        <div v-if="$route.params.id != 0" class="select-car-block"><car-post-component
                :item="carPosts.find(item => item.id == $route.params.id)"></car-post-component></div>

        <div class="quiz-block">
            <custom-progress :value="progressValue" />
            <keep-alive>
                <component :is="component" @earning="SetEarning" @budget="SetBudget" @timeEarning="SetTimeEarning"
                    @aboutEmploy="SetAboutEmploy" @employmentStatus="SetEmploymentStatus" @back="backComponent"
                    @info="SetInfo" @address="SetAddress" @workingStatus="SetWorkingStatus" @born="SetBorn"
                    @longReceiving="SetLongReceiving" @howEarnIncome = SetEarnIncome>
                </component>
            </keep-alive>
        </div>
    </div>
</template>
<script>
import customProgress from '@/components/progress-bar.vue';
import howLongHaveYouBeenEarningThisIncome from '@/components/405-page/how-long-earning-income.vue';
import lastStepComponent from '@/components/405-page/last-step-component.vue';
import whereDoYouLiveComponent from '@/components/405-page/where-do-you-live-component.vue';
import howLongReceivingComponent from '@/components/405-page/how-long-receiving-component.vue';
import monthlyIncomeComponent from '@/components/405-page/monthly-income-component.vue';
import budgetComponent from '@/components/405-page/budget-component.vue';
import employmentStatusComponent from '@/components/405-page/employment-status-component.vue';
import { shallowRef } from 'vue';
import earningIncomeComponent from '@/components/405-page/earning-income-component.vue';
import carPostComponent from '@/components/posts/car-post-component.vue';
import carPosts from '@/js/car-posts';
import SuccesfulComponent from '@/components/405-page/succesful-component.vue';
import whenWereYouBornComponent from '@/components/405-page/when-were-you-born-component.vue';
import tellUsAboutEmployment from '@/components/405-page/tell-us-about-employment.vue';
import areYouWorking from '@/components/405-page/are-you-working.vue';
export default {
    components: { carPostComponent, howLongHaveYouBeenEarningThisIncome, customProgress },
    data() {
        return {
            carPosts,
            component: shallowRef(budgetComponent),
            budget: '',
            employedStatus: '',
            earning: '',
            workingStatus: '',
            howEarnIncome: '',
            howLongReceiving: '',
            history: [],
            progressHistory: [],
            address: '',
            longReceiving: '',
            aboutEmploy: '',
            progressValue: 10,
        }
    },
    props: {
        selectCar: {
            type: String,
            default: ''
        }
    },
    methods: {
        SetBudget(value) {
            this.budget = value;
            this.SetComponent(employmentStatusComponent, this.progressValue);
            this.progressValue = 20;
        },
        SetEmploymentStatus(value) {
            this.employedStatus = value;
            if (value === 'Other') {
                this.SetComponent(monthlyIncomeComponent, this.progressValue);
            }
            else if (value === 'Student') {
                this.SetComponent(areYouWorking, this.progressValue)
            }
            else {
                this.SetComponent(earningIncomeComponent, this.progressValue);
            }
            this.progressValue = 30;
        },
        SetEarning(value) {
            this.earning = value;
            if (this.employedStatus === 'Self-Employed' || this.employedStatus === 'Retired / Pension') {
                this.SetComponent(howLongReceivingComponent, this.progressValue);
            }
            else if (this.employedStatus === 'Employed') {
                this.SetComponent(tellUsAboutEmployment, this.progressValue);
            }
            else{
                this.SetComponent(howLongReceivingComponent, this.progressValue);
            }
            this.progressValue = 40;
        },
        SetWorkingStatus(value) {
            this.workingStatus = value;
            if (value === 'Yes') {
                this.SetComponent(tellUsAboutEmployment, this.progressValue);
                this.progressValue = 40;
            }
            else {
                this.SetComponent(howLongReceivingComponent, this.progressValue);
                this.progressValue = 40;
            }
        },
        SetTimeEarning(value) {
            this.timeEarning = value;
            this.SetComponent(howLongReceivingComponent);
        },
        SetAboutEmploy(value) {
            this.aboutEmploy = value;
            if (this.employedStatus === 'Student') {
                this.SetComponent(whereDoYouLiveComponent, this.progressValue);
                this.progressValue = 70;
            }
            else if (this.employedStatus === 'Employed') {
                this.SetComponent(whereDoYouLiveComponent);
            }
        },
        SetLongReceiving(value) {
            this.longReceiving = value;
            this.SetComponent(whereDoYouLiveComponent, this.progressValue);
            this.progressValue = 50;
        },
        SetAddress(value) {
            this.address = value;
            this.SetComponent(whenWereYouBornComponent, this.progressValue);
            this.progressValue = 80;
        },
        SetBorn(value) {
            this.born = value;
            this.SetComponent(lastStepComponent, this.progressValue);
            this.progressValue = 90;
        },
        SetInfo(value) {
            this.infoPerson = value;
            this.SetComponent(SuccesfulComponent, this.progressValue)
            this.progressValue = 100;
        },
        backComponent() {
            this.component = this.history.pop();
            this.progressValue = this.progressHistory.pop();

        },
        SetEarnIncome(value){
            this.howEarnIncome = value;
            this.SetComponent(earningIncomeComponent, this.progressValue)
            this.progressValue = 40;
        },
        SetComponent(component, progress) {
            this.history.push(this.component);
            this.component = component;
            this.progressHistory.push(progress);
        },
    },
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/default/default.scss";

.page.content-container {
    display: flex;
    justify-content: center;
    gap: 60px;
    @media (max-width: 968px) {
        flex-direction: column-reverse;
        align-items: center;
        margin-bottom: 50px;
    }
    .select-car-block {
        max-width: 420px;
        max-height: 395px;
        width: 100%;
        height: 100%;
        @media (max-width: 968px) {
        max-width: 454px;
    }
    }

    .quiz-block {
        max-width: 454px;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 60px;

        #progress {
            width: 100%;
        }

    }
}</style>