<template>
    <div class="employment-status-block">
        <h4 class="titleH4">What's your employment status?</h4>
        <p class="description">Your employment status will help determine the best vehicle and financing options for you.
        </p>
        <div class="buttons-block">
            <base-button @click="selectValue('Employed')" :class="{ 'select': value == 'Employed' }" quiz>Employed</base-button>
            <base-button @click="selectValue('Self-Employed')" :class="{ 'select': value == 'Self-Employed' }" quiz>Self-Employed</base-button>
            <base-button @click="selectValue('Student')" :class="{ 'select': value == 'Student' }" quiz>Student</base-button>
            <base-button @click="selectValue('Retired / Pension')" :class="{ 'select': value == 'Retired / Pension' }" quiz>Retired / Pension</base-button>
            <base-button @click="selectValue('Other')" :class="{ 'select': value == 'Other' }" quiz>Other</base-button>
            <div></div>
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
            <backControl @click="back"></backControl>
        </div>
    </div>
</template>
<script>
import backControl from '../ui/back-control.vue';
import baseButton from '../ui/base-button.vue'
export default {
    components: { baseButton, backControl },
    data() {
        return {
            value: '',
        }
    },
    computed: {
        disabled() {
            if (this.value != '') return false;
            else return true;
        },
    },
    methods: {
        selectValue(value) {
            this.value = value;
        },
        sendValue() {
            this.$emit('employment-status', this.value);
        },
        back() {
            this.$emit('back');
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";
</style>