<template>
    <div class="employment-status-block">
        <h4 class="titleH4">Congratulations! Last step.</h4>
        <p class="description">Get access to your vehicle and financing options by creating your account. There is no
            obligation to continue with financing if you change your mind. </p>
        <div class="input-block">
            <input class="input street" v-model="street" placeholder="First name">
            <input class="input city" v-model="city" placeholder="Last name">
            <input class="input province" v-model="province" placeholder="Email">
            <input class="input postal" v-model="postal" placeholder="Phone number">
        </div>
        <div class="buttons-block">
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
            <backControl @click="back"></backControl>
        </div>
    </div>
</template>
<script>
import backControl from '../ui/back-control.vue';
import baseButton from '../ui/base-button.vue'
export default {
    components: { baseButton, backControl },
    data() {
        return {
            street: '',
            city: '',
            province: '',
            postal: '',
        }
    },
    computed: {
        disabled() {
            if (this.street != '' && this.city != '' && this.province != '' && this.postal != '') return false;
            else return true;
        },
        address() {
            return this.street + ' ' + this.city + ' ' + this.province + ' ' + this.postal;
        }
    },
    methods: {
        sendValue() {
            this.$emit('info', this.address);
        },
        back() {
            this.$emit('back');
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";

.input-block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;

    .street {
        grid-column-start: 1;
        grid-column-end: 3;
    }

    .city {
        grid-column-start: 1;
        grid-column-end: 3;
    }

    .province {
        grid-column-start: 1;
        grid-column-end: 2;
    }

}</style>