<template>
    <div class="employment-status-block">
        <h4 class="titleH4">Tell us about your employment</h4>
        <p class="description">We won’t come visit but we need these details to confirm your employment status so you can
            get the best rates possible.</p>
        <div class="input-block">
            <input class="input employer" v-model="employer" placeholder="Employer">
            <input class="input title" v-model="title" placeholder="Title">
        </div>
        <div class="buttons-block">
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
            <backControl @click="back"></backControl>
        </div>
    </div>
</template>
<script>
import backControl from '../ui/back-control.vue';
import baseButton from '../ui/base-button.vue'
export default {
    components: { baseButton, backControl },
    data() {
        return {
            employer: '',
            title: '',
        }
    },
    computed: {
        disabled() {
            if (this.employer != '' && this.title != '') return false;
            else return true;
        },
        timeEarning() {
            return this.employer + ' ' + this.title;
        }
    },
    methods: {
        sendValue() {
            this.$emit('aboutEmploy', this.timeEarning);
            console.log(this.timeEarning);
        },
        back() {
            this.$emit('back');
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";

.input-block {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 10px;
}
</style>