<template>
    <div class="employment-status-block">
        <h4 class="titleH4">Where do you live?</h4>
        <p class="description">Providing your location helps find the best deals near you.</p>
        <div class="input-block">
            <input id="street" class="input street" :class="{ error: v$.street.$error }" v-model="street" placeholder="Street address">
            <input id="city" class="input city" :class="{ error: v$.city.$error }" v-model="city" placeholder="City">
            <input id="province" class="input province" :class="{ error: v$.province.$error }" v-model="province" placeholder="Province">
            <input id="postal" class="input postal" :class="{ error: v$.postal.$error }" v-model="postal" placeholder="Postal code">
        </div>
        <div class="buttons-block">
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
            <backControl @click="back"></backControl>
        </div>
    </div>
</template>
<script>
import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'
import backControl from '../ui/back-control.vue';
import baseButton from '../ui/base-button.vue'
export default {
    components: { baseButton, backControl },
    setup() {
        return {
            v$: useVuelidate()
        }
    },
    data() {
        return {
            street:'',
            city:'',
            province:'',
            postal:'',
        }
    },
    validations() {
        return {
            street: { required },
            city: { required },
            province: { required },
            postal: { required }
        }
    },
    computed: {
        disabled() {
            if (this.street != '' && this.city != '' && this.province != ''  && this.postal != '' ) return false;
            else return true;
        },
        address(){
            return this.street + ' ' + this.city + ' ' + this.province + ' ' + this.postal;
        }
    },
    methods: {
        async sendValue() {
            const isForm = await this.v$.$validate();
            if(isForm){
                this.$emit('address', this.address);
            }
        },
        back() {
            this.$emit('back');
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";
.input-block{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 10px;
    .street{
        grid-column-start: 1;
        grid-column-end: 3;
    }
    .city{
        grid-column-start: 1;
        grid-column-end: 3;
    }
    .province{
        grid-column-start: 1;
        grid-column-end: 2;
    }

}
</style>