import { createRouter, createWebHistory } from "vue-router";
import homePage from "@/pages/home-page.vue";
import aboutPage from "@/pages/about-page.vue";
import termsPage from "@/pages/terms-page.vue";
import privacyPage from "@/pages/privacy-page.vue";
import calculatorPage from "@/pages/calculator-page.vue";
import loanRatesPage from "@/pages/loan-rates-page.vue";
import vehiclesVideoPage from "@/pages/vehicles-video-page.vue";
import blogPage from "@/pages/blog-page.vue";
import postIdPage from "@/pages/post-id-page.vue";
import headerComponent from "@/components/header/header-component.vue";
import headerInventoryComponent from "@/components/header/headerInventoryComponent.vue";
import footerComponent from "@/components/footer-component.vue";
import notFound from "@/pages/404-page.vue";
import page from "@/pages/web-page.vue";
import inventoryPage from "@/pages/inventory-page.vue";
import quest from "@/pages/405-page.vue";
import carIdPage from "@/pages/car-id-page.vue";
import headerCarPage from "@/components/header/headerCarPage.vue";

const routes = [
  {
    path: "/",
    components: {
      header: headerComponent,
      default: page,
      footer: footerComponent,
    },
    children: [
      {
        path: "",
        component: homePage,
      },
      {
        path: "about",
        name: "about",
        component: aboutPage,
      },
      {
        path: "calculator",
        component: calculatorPage,
      },
      {
        path: "loanRates",
        name: "content",
        component: loanRatesPage,
      },
      {
        path: "video",
        name: "video",
        component: vehiclesVideoPage,
      },
      {
        path: "blog",
        name: "blog",
        component: blogPage,
      },
      {
        path: "terms",
        name: "Terms&Conditions",
        component: termsPage,
      },
      {
        path: "privacy",
        name: "privacyPolicy",
        component: privacyPage,
      },
      {
        path: "blog/:id",
        component: postIdPage,
      },
    ],
  },
  { path: "/:pathMatch(.*)*", name: "notFound", component: notFound },
  {
    path: "/quiz/:id",
    name: "405",
    components: { header: headerComponent, default: quest },
  },
  {
    path: "/inventory",
    components: { header: headerInventoryComponent, default: inventoryPage, footer: footerComponent, }
  },
  {
    path: "/inventory/:id",
    components: { header: headerCarPage, default: carIdPage, footer: footerComponent, }
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  window.scroll(0, 0);
  document.querySelector("#preloader").classList.add("show");
  document.body.style.overflowY = "hidden";

  next();
});

router.afterEach(() => {
  setTimeout(() => {
    document.body.style.overflowY = "auto";
  }, 1000);

  document.querySelector("#preloader").classList.remove("show");
});

export default router;
