<template>
    <div class="postid-page" id="post">
        <div class="page content-container">
            <div class="title-block">
                <h1 class="title">{{ selectedItem.title }}</h1>
                <p class="input-text">Watching now 8 people</p>
            </div>
            <div class="blocks-page">
                <div class="first-block">
                    <div class="thumb-example">
                        <swiper class="top-swiper" :style="{
                            '--swiper-navigation-color': '#fff',
                            '--swiper-pagination-color': '#fff'
                        }" :modules="modules" :space-between="10" :thumbs="{ swiper: thumbsSwiper }">

                            <swiper-slide class="slide" v-for="index in selectTypeSlides" :key="index">
                                <img :src="imagePath + index" />
                            </swiper-slide>
                            <div class="slider-nav-buttons">
                                <div class="buttons-block">
                                    <div class="nav-button exterior" :class="{ 'active': exterior }"
                                        @click="this.exterior = true">Exterior</div>
                                    <div class="nav-button interior" :class="{ 'active': !exterior }"
                                        @click="this.exterior = false">Interior</div>
                                </div>
                                <button class="button-icon" @click="copyLink"></button>
                            </div>
                        </swiper>
                        <swiper class="thumbs-swiper" ref="thumbSwiper" :navigation="true" :modules="modules"
                            :space-between="10" :slides-per-view="4" :watch-slides-progress="true" :prevent-clicks="false"
                            :prevent-clicks-propagation="false" @swiper="setThumbsSwiper">
                            <swiper-slide class="slide" v-for="index in selectTypeSlides" :key="index">
                                <img :src="imagePath + index" />
                            </swiper-slide>

                        </swiper>
                        <router-link :to=openQuiz><base-button :padding-mobile="'10px 10px 10px 10px'" background
                                class="buttonRequest">apply for this vehicle</base-button></router-link>
                    </div>
                    <div class="information-blocks">
                        <div class="info-car-block">
                            <div class="info-item-block">
                                <p class="input-text">Price</p>
                                <p class="info-value price">{{ selectedItem.price }} $</p>
                            </div>
                            <div class="info-item-block">
                                <p class="input-text">Body</p>
                                <p class="info-value">{{ selectedItem.type }}</p>
                            </div>
                            <div class="info-item-block">
                                <p class="input-text">Year</p>
                                <p class="info-value">{{ selectedItem.year }}</p>
                            </div>
                            <div class="info-item-block">
                                <p class="input-text">Transmission</p>
                                <p class="info-value">{{ selectedItem.transmission }}</p>
                            </div>
                            <div class="info-item-block">
                                <p class="input-text">Kilometres</p>
                                <p class="info-value">{{ selectedItem.kilometres }}</p>
                            </div>
                        </div>
                        <hr>
                        <div class="info-car-block grid-row">
                            <p class="input-text">Detail</p>
                            <p class="detail-text">Rear wheel drive</p>
                            <p class="detail-text">2500-4000 rpm</p>
                            <p class="detail-text">6 cylinders</p>
                            <p class="detail-text">0-100 in 2.7 s.</p>
                            <p class="detail-text">11 l. per 100 km.</p>
                            <p class="detail-text">Power steering</p>
                            <p class="detail-text">Rear wheel drive</p>
                            <p class="detail-text">2500-4000 rpm</p>
                        </div>
                        <hr>
                        <div class="info-car-description-block">
                            <p class="input-text">Description</p>
                            <p class="description">Porsche 911 - a sports car manufactured by the German company Porsche AG
                                in
                                the back of a two-door coupe or a convertible based on it, produced in different generations
                                from 1964 to the present day.The 911 index was not originally planned for the timeless
                                designation of different generations of the same car, and was no more than one of many
                                similar
                                in the through three-digit internal factory numbering of all Porsche models assigned to a
                                very
                                specific 1964 model.</p>
                            <router-link :to="'/quiz/0'"><base-button class="base-button" background>Request more
                                    information</base-button></router-link>
                        </div>
                    </div>
                </div>
                <calculator-text-component title></calculator-text-component>
            </div>
        </div>
    </div>
</template>
  
<script>
import calculatorTextComponent from '@/components/calculator/calculator-text-component.vue';
import baseButton from '@/components/ui/base-button.vue';
import { useRoute } from 'vue-router'
import carPosts from '@/js/car-posts';
import { ref } from 'vue'
import { Navigation, Thumbs } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/vue'
import 'swiper/css'
import 'swiper/css/navigation'
import 'swiper/css/thumbs'

export default {
    name: 'post-page',
    data() {
        return {
            carPosts,
            exterior: true,
        }
    },
    components: {
        Swiper,
        SwiperSlide,
        baseButton,
        calculatorTextComponent
    },
    methods:{
        copyLink() {
            const url = window.location.href;
            navigator.clipboard.writeText(url)
            .catch((error) => {
                console.error('Не вдалося скопіювати посилання: ', error);
            });
        },
    },
    computed: {
        selectedItem() {
            return this.carPosts.find(item => item.id === this.route);
        },
        route() {
            return Number(useRoute().params.id)
        },
        imagePath() {
            return process.env.BASE_URL;
        },
        openQuiz() {
            console.log(this.$route);
            return `/quiz/${this.$route.params.id}`;
        },
        selectTypeSlides() {
            if (this.exterior === true) {
                return this.selectedItem.Exterior;
            }
            else {
                return this.selectedItem.Interior;
            }
        }
    },
    setup() {
        const thumbsSwiper = ref();
        const setThumbsSwiper = function (swiper) {
            thumbsSwiper.value = swiper;
        };

        return {
            modules: [Navigation, Thumbs],
            setThumbsSwiper: setThumbsSwiper,
            thumbsSwiper: thumbsSwiper
        };
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/default/default.scss";


.blocks-page {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 220px;

    @media (max-width: 968px) {
        gap: 160px;
    }

    @media (max-width: 600px) {
        gap: 80px;
    }

}

.content-container {
    @media (max-width: 968px) {
        padding-left: 85px;
        padding-right: 85px;
    }

    @media (max-width: 600px) {
        padding-left: 20px;
        padding-right: 20px;
    }
}

.title-block {
    .title {
        @include titleH2;
    }

    .input-text {
        @include input-text;
    }
}

.first-block {
    display: flex;
    gap: 40px;
    min-width: 0;

    @media (max-width: 968px) {
        flex-direction: column;
        gap: 160px;
    }

    @media (max-width: 600px) {
        gap: 80px;
    }

    .information-blocks {
        display: flex;
        flex-direction: column;
        gap: 60px;
        width: 100%;

        @media (max-width: 600px) {
            gap: 40px;
        }

        hr {
            width: 100%;
        }

        .info-car-description-block {
            .input-text {
                @include input-text;
            }

            .description {
                margin-top: 20px;
            }

            .base-button {
                margin-top: 40px;
            }
        }

        .info-car-block {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            height: fit-content;
            row-gap: 20px;
            column-gap: 175px;

            @media (max-width: 600px) {
                column-gap: 100px;

                &.grid-row {
                    grid-template-columns: repeat(1, 1fr);
                }
            }

            .detail-text {
                @include titleH5;
            }

            &.grid-row {
                :first-child {
                    @media (max-width: 600px) {
                        grid-column-end: 2;
                    }
                }
            }

            :first-child {
                grid-column-start: 1;
                grid-column-end: 3;
            }

            .input-text {
                @include input-text;
            }

            .info-item-block {
                width: fit-content;
                height: fit-content;



                .info-value {
                    @include titleH4;
                }

                .price {
                    color: #7481FF;
                }
            }
        }
    }
}

.thumb-example {
    min-width: 0;
    max-width: 650px;
    max-height: 450px;
    height: fit-content;
    background-color: $color3;
    width: 100%;

    @media (max-width: 968px) {
        max-height: none;
        max-width: none;
    }
}

.top-swiper,
.thumbs-swiper {

    .slide {
        background-color: black;

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 1px;

        }
    }
}

.buttonRequest {
    display: none;

    @media (max-width: 600px) {
        display: block;
        margin-top: 20px;
        width: 100%;
    }
}

.top-swiper {
    height: 80%;
    width: 100%;
    position: relative;
}

.thumbs-swiper {
    max-height: 20%;
    box-sizing: border-box;
    margin-top: 20px;
    padding: 0;
    position: relative;
    height: 100%;

    &.swiper-button-prev {
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        background-color: red;
        z-index: 1111;
    }

    .slide {

        max-height: 151px;
        width: 25%;
        height: 100%;
        opacity: 1;
        border-radius: 2px;
        overflow: hidden;



        &:not(.swiper-slide-thumb-active) {
            img {
                opacity: 0.7;
            }
        }

        &.swiper-slide-thumb-active {
            border: 1px solid #7481FF;
        }
    }
}

.slider-nav-buttons {
    padding-left: 20px;
    padding-right: 20px;
    position: absolute;
    width: 100%;
    height: 55px;
    z-index: 5;
    background-color: none;
    pointer-events: none;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-between;

    .buttons-block {
        display: flex;
        gap: 7px;
    }

    .nav-button {
        width: fit-content;
        height: fit-content;
        pointer-events: visible;
        display: block;
        background: rgba(215, 215, 215, 0.2);
        @include buttonText;
        color: $color3;
        padding: 10px 20px;
        border-radius: 2px;
        cursor: pointer;

        &.active {
            background-color: $color3;
            color: #41456B;
        }
    }

    .nav-button.interior {}

    .button-icon {
        pointer-events: visible;
        width: 30px;
        height: 30px;
        border: none;
        background: none;
        @include shareIc;
        color: $color3;
        font-size: 22px;
        cursor: pointer;
    }
}</style>
