<template>
    <div class="employment-status-block">
        <h4 class="titleH4">When were you born?</h4>
        <div class="input-block">
            <input id="year" class="input year" :class="{ error: v$.year.$error }" v-model="year" placeholder="Year">
            <input id="month" class="input month" :class="{ error: v$.month.$error }" v-model="month" placeholder="Month">
            <input id="day" class="input day" :class="{ error: v$.day.$error }" v-model="day" placeholder="Day">
        </div>
        <div class="buttons-block">
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
            <backControl @click="back"></backControl>
        </div>
    </div>
</template>
<script>
import { useVuelidate } from '@vuelidate/core'
import { required, numeric, between } from '@vuelidate/validators'
import backControl from '../ui/back-control.vue';
import baseButton from '../ui/base-button.vue'
export default {
    components: { baseButton, backControl },
    setup() {
        return {
            v$: useVuelidate()
        }
    },
    data() {
        return {
            year: null,
            month: null,
            day: null,
        }
    },
    validations() {
        return {
            year: { required, numeric, between: between(1900, 2023), },
            month: { required, numeric, between: between(1, 12), },
            day: { required, numeric, between: between(1, 31), },
        }
    },
    computed: {
        disabled() {
            if (this.year != '' && this.month != '' && this.day != '') return false;
            else return true;
        },
        born() {
            return this.year + ' ' + this.month + ' ' + this.day;
        }
    },
    methods: {
        async sendValue() {
            const isForm = await this.v$.$validate();
            if (isForm) {
                this.$emit('born', this.born);
            }
        },
        back() {
            this.$emit('back');
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";

.input-block {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;

    .year {
        grid-column-start: 1;
        grid-column-end: 3;
    }
}
</style>